div.nav-container {
    display: grid;
    background-color: #99DAF7;
    /* height: auto; */
    width: 100%;
    margin: 0;

    grid-template-columns: max-content minmax(min-content, auto) repeat(7, max-content);
    align-items: center;
}
div.nav-container h2 {
   
    color: #35ABEE;
    font-size: 35px;
    
    display: inline-flex;
    margin-left: 50px;
    margin-right: 20px;
    width: auto;
    height: auto;
}

div.nav-container a, div.nav-container > p {
    color: #35ABEE;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    
    display: inline-flex;
    align-items: stretch;
    margin-top: -10px;
    margin-right: 30px;
    margin-left: 30px;
}

div.nav-container img {
   display: inline-flex;
    width: auto;
    height: auto;
    margin-right: 100px;
}
